<template>
    <div>
        <van-nav-bar
                title=""
                left-text=""
                left-arrow
                @click-left="onClickLeft"
                class="clock_in"
        />
        <div style="margin-top: 46px">
        <van-field
                v-model="message"
                autosize
                type="textarea"
                maxlength="20"
                placeholder=""
                show-word-limit
        />
        <quill-editor ref="myTextEditor"
                      :content="content"
                      :options="editorOption"
                      @change="onEditorChange($event)">
        </quill-editor>
        <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" @click="submit">
                
            </van-button>
        </div>
        </div>
    </div>
</template>

<script>
    import {quillEditor} from 'vue-quill-editor'
    import 'quill/dist/quill.snow.css'
    export default {
        name: "Publicactive",
        components:{
            quillEditor
        },
        data(){
            return{
                content: "",
                message:'',
                editorOption: {
                    placeholder: '...',
                    modules:{toolbar:[
                            ['bold', 'italic', 'underline', 'image'],
                            [{ 'size': ['small', false, 'large', 'huge'] }],
                            [{ 'color': [] }],
                            [{ 'align': [] }],
                        ]}
                }
            }
        },
        methods:{
            onClickLeft(){
                this.$router.push({
                    path:'/',
                    query:{
                        active:2
                    }
                })
            },
            onEditorChange({html}) {
                this.content = html;
            },
            submit(){
                const params={
                    'key':'vv9544',
                    'title':this.message,
                    'content':this.content,
                    'email':this.$getCookie(' email'),
                    'nickname':this.$getCookie('username')
                }
                this.$axios.post('https://zhuzuoji.com/myapi/blog/addblog',this.$qs.stringify(params))
                    .then(res=>{
                        console.log(res)
                        if(res.status==200&&res.data.state){
                            this.$setCookie('integral',res.data.new_inte,7)
                            this.$toast.success(res.data.msg+',+5')
                            this.$router.push({
                                path:'/',
                                query:{
                                    active:2
                                }
                            })
                        }else {
                            this.$toast.fail(res.data.msg)
                        }
                    }).catch(err=>{
                    let error=err.toString()
                    if(error.indexOf('Network')!=-1){
                        this.$toast.fail('')
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .clock_in {
        background-color: rgb(146,188,255);
        position: fixed;
        width: 100%;
        height: 46px;
        top: 0;
    }
    >>>.van-nav-bar__text{
        color: #fff;
    }
    >>>.van-icon::before{
        color: #fff;
    }
    >>>.van-nav-bar__title{
        color: #fff;
    }
</style>